import React from "react";
import Obfuscate from "../../components/obfuscate.js";
import ClearIcon from '@mui/icons-material/Clear';
import BareClient from '@tomphttp/bare-client';

function Tab() {
const bare = new BareClient(window.location.origin + "/bare/");

var [ urlValue, setUrlValue ] = React.useState("")
var [ titleValue, setTitleValue ] = React.useState(localStorage.getItem("title") || "")
var [ iconValue, setIconValue ] = React.useState(localStorage.getItem("icon") ? localStorage.getItem("icon").split("#")[1] : "")

function tabReset() {
  localStorage.removeItem("title")
  localStorage.removeItem("icon")
  document.title = "Settings | Metallic"
  document.querySelector("link[rel='icon']").href = "/images/logo.svg"
  setTitleValue("")
  setIconValue("")
}

function setTitle(title) {
  if (title !== "") {
  localStorage.setItem("title", title)
  document.title = title
  } else {
  localStorage.removeItem("title")
  document.title = "Settings | Metallic"
  }
}

async function setIcon(icon) {
  if (icon !== "") {
  var getIcon = await bare.fetch(icon);
  var iconBlob = new Blob([await getIcon.arrayBuffer()], {
    type: getIcon.headers.get('content-type')
  })
  var reader = new FileReader();
  reader.readAsDataURL(iconBlob);
  reader.addEventListener('load', function() {
    localStorage.setItem("icon", reader.result + "#" + icon)
    document.querySelector("link[rel='icon']").href = reader.result + "#" + icon
  })
  } else {
  localStorage.removeItem("icon")
  document.querySelector("link[rel='icon']").href = "/images/logo.svg"
  }
}

async function setTab(url) {
if (url == null || url == "") {
return tabReset();
}
var title = url;
var favicon = ""
var site = await bare.fetch(url)
var code = await site.text()
var parser = new DOMParser();
var doc = parser.parseFromString(code, "text/html");
if (doc.getElementsByTagName("title")[0]) {
title = doc.getElementsByTagName("title")[0].innerText
}
if (doc.querySelector("link[rel='shortcut icon']")) {
favicon = doc.querySelector("link[rel='shortcut icon']").href
}
if (doc.querySelector("link[rel='icon']")) {
favicon = doc.querySelector("link[rel='icon']").href
}
setTitle(title)
setIcon(favicon)
setTitleValue(title)
setIconValue(favicon)
setUrlValue("")
}

function quickTab(event) {
  var newTab = {title: null, icon: null};
  switch (event.target.dataset.tab) {
  case "google":
    newTab.title = "Google"
    newTab.icon = "https://www.google.com/favicon.ico"
    break;
  case "google drive":
    newTab.title = "Google Drive"
    newTab.icon = "https://www.drive.google.com/favicon.ico"
    break;
  case "edpuzzle":
    newTab.title = "Edpuzzle"
    newTab.icon = "https://edpuzzle.imgix.net/favicons/favicon-32.png"
    break;
  case "zoom":
      newTab.title = "Zoom"
      newTab.icon = "https://st1.zoom.us/zoom.ico"
      break;
  case "khan academy":
    newTab.title = "Edpuzzle"
    newTab.icon = "https://www.khanacademy.org/favicon.ico"
    break;
  default:
    newTab.title = ""
    newTab.icon = ""
    break;
}

setTitle(newTab.title)
setIcon(newTab.icon)
setTitleValue(newTab.title)
setIconValue(newTab.icon)
}

  return (
      <React.Fragment>
      <div className="optiontitle"><Obfuscate>Tab Cloak</Obfuscate></div>
      <div className="chooseoption">
      <div className="optionchoosecircle" onClick={tabReset}><ClearIcon style={{pointerEvents: "none"}} fontSize="medium" /></div>
      <div data-tab="google" onClick={quickTab} className="optionchoosecircle"><img style={{pointerEvents: "none"}} src="data:image/x-icon;base64,#https://www.google.com/favicon.ico" /></div>
      <div data-tab="google drive" onClick={quickTab} className="optionchoosecircle"><img style={{pointerEvents: "none"}} src="data:image/png;base64,#https://www.drive.google.com/favicon.ico" /></div>
      <div data-tab="edpuzzle" onClick={quickTab} className="optionchoosecircle"><img style={{pointerEvents: "none"}} src="data:image/png;base64,#https://edpuzzle.imgix.net/favicons/favicon-32.png" /></div>
      <div data-tab="zoom" onClick={quickTab} className="optionchoosecircle"><img style={{pointerEvents: "none"}} src="data:image/x-icon;base64,#https://st1.zoom.us/zoom.ico" /></div>
      <div data-tab="khan academy" onClick={quickTab} className="optionchoosecircle"><img style={{pointerEvents: "none"}} src="data:image/vnd.microsoft.icon;base64,#https://www.khanacademy.org/favicon.ico" /></div>
      </div>
      <div className="optiontitle"><Obfuscate>URL</Obfuscate></div>
      <input autoComplete="off" placeholder="Enter a URL" className="optionchooseinput" onKeyDown={(e) => {if (e.key == 'Enter') {setTab(e.target.value)}}} value={urlValue} onChange={(e) => setUrlValue(e.target.value)} />
      <div className="optiontitle"><Obfuscate>Custom</Obfuscate></div>
      <input autoComplete="off" placeholder="Enter a title" className="optionchooseinput" onKeyUp={(e) => setTitle(e.target.value)} value={titleValue} onChange={(e) => setTitleValue(e.target.value)} />
      <input autoComplete="off" placeholder="Enter an icon URL" className="optionchooseinput" onKeyUp={(e) => setIcon(e.target.value)} value={iconValue} onChange={(e) => setIconValue(e.target.value)} />
      </React.Fragment>
  );
}

export default Tab;