import React from "react";
import { Link } from "react-router-dom";
import Nav from "../components/nav.js";
import Head from "../components/head.js";
import Obfuscate from "../components/obfuscate.js";
import Proxy from "../components/proxy.js";

import gitems from "../data/apps.json"

function Apps() {
  var [proxyConfig, setProxyConfig] = React.useState({
    open: false,
    url: null,
    title: null,
    icon: null
  })

  function goApp(config) {
    try {
    var getLink = window['getLink']
    setProxyConfig({
      open: true,
      url: getLink(config.url),
      title: null,
      icon: null
    })
    } catch {
    }
  }

  var [ appsSearchTerm, setAppsSearchTerm ] = React.useState("")

  function searchAppsType(e) {
    setAppsSearchTerm(e.target.value.toLowerCase())
  }

  const gitemssearched = gitems.filter((item) => {
    if (!appsSearchTerm) {
      return item;
    } else {
      return item.name.toLowerCase().includes(appsSearchTerm)
    }
})
  return (
      <React.Fragment>
      <Head defaultTitle="Apps | Metallic"></Head>
      <Nav />
      <Proxy config={proxyConfig} configChange={(e) => setProxyConfig(e)} />
      <div className="omniboxcontainersearch omniboxcontainer">
      <div className="omnisearch omnibox">
      <div className="searchicon"><svg focusable="false" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M15.5 14h-.79l-.28-.27A6.471 6.471 0 0 0 16 9.5 6.5 6.5 0 1 0 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"></path></svg></div>
      <input onKeyUp={searchAppsType} autoComplete="off" autoFocus id="search" className="search" />
      </div>
      </div>
      {!gitemssearched.length ? <div className="desc">No results found.</div>: ""}
      <div className="gitems">
      {gitemssearched.map((item, key) => {
        function getGlobeImg() {
          var code = `<svg width="24" height="24" xmlns="http://www.w3.org/2000/svg"><path d="M12 2c5.52 0 10 4.48 10 10s-4.48 10-10 10S2 17.52 2 12 6.48 2 12 2zM4 12h4.4c3.407.022 4.922 1.73 4.543 5.127H9.488v2.47a8.004 8.004 0 0010.498-8.083C19.327 12.504 18.332 13 17 13c-2.137 0-3.206-.916-3.206-2.75h-3.748c-.274-2.728.683-4.092 2.87-4.092 0-.975.327-1.597.811-1.97A8.004 8.004 0 004 12z" fill="%COLOR%"/></svg>`
          code = code.replace("%COLOR%", getComputedStyle(document.body).getPropertyValue('--background').replaceAll(" ", ""))
          return "data:image/svg+xml;base64," + window.btoa(code)
        }
        var gbackground = "var(--gimg) 0% 0% / cover, url(" + getGlobeImg() + ") 0% 0% / cover, var(--highlight)"
          return (
            <div onClick={() => goApp(item)} style={{"--gimg": "url(" + item.icon + ")", background: gbackground}} className="gitem" key={key}>
            <div className="gtext"><Obfuscate>{item.name}</Obfuscate></div>
            </div>
          );
      })}
      </div>
      </React.Fragment>
  );
}

export default Apps;