import React from "react";
import Obfuscate from "../../components/obfuscate.js";

function UI() {
  const [currentWindow, setCurrentWindow] = React.useState(localStorage.getItem("window") || "default")
  function windowClass(type) {
    return currentWindow == type ? "optionchoose chooseactive" : "optionchoose"
  }
  function setWindow(event) {
    localStorage.setItem("window", event.target.dataset.tab)
    setCurrentWindow(event.target.dataset.tab)
  }
  const [currentControls, setCurrentControls] = React.useState(localStorage.getItem("controls") || "default")
  function controlsClass(type) {
    return currentControls == type ? "optionchoose chooseactive" : "optionchoose"
  }
  function setControls(event) {
    localStorage.setItem("controls", event.target.dataset.tab)
    setCurrentControls(event.target.dataset.tab)
  }
  return (
      <React.Fragment>
      <div className="optiontitle"><Obfuscate>Window</Obfuscate></div>
      <div className="chooseoption">
      <div data-tab="default" onClick={setWindow} className={windowClass("default")}><Obfuscate>Default</Obfuscate></div>
      <div data-tab="simple" onClick={setWindow} className={windowClass("simple")}><Obfuscate>Simple</Obfuscate></div>
      <div data-tab="ab" onClick={setWindow} className={windowClass("ab")}><Obfuscate>About:Blank</Obfuscate></div>
      </div>
      <div className="optiontitle"><Obfuscate>Controls</Obfuscate></div>
      <div className="chooseoption">
      <div data-tab="default" onClick={setControls} className={controlsClass("default")}><Obfuscate>Default</Obfuscate></div>
      <div data-tab="classic" onClick={setControls} className={controlsClass("classic")}><Obfuscate>Classic</Obfuscate></div>
      </div>
      </React.Fragment>
  );
}

export default UI;