import React from "react";

const hiddenClass = randomLetter()

function randomLetter(not) {
    var random = String.fromCharCode((!Math.round(Math.random()) ? 65 : 97) + Math.floor(Math.random() * 26));

    if (random !== not) {
    return random;
    } else {
    return randomLetter(not)
    }
}

function Obfuscate(props) {
    var text = Array.isArray(props.children) ? props.children.join("") : props.children
    var result = document.createElement("span")
    result.className = randomLetter(hiddenClass)
    var words = text.split(" ")
    
    for (var word in words) {
    var wordspan = document.createElement("span")
    wordspan.className = randomLetter(hiddenClass)
    var letters = words[word].split("")
    
    for (var letter in letters) {
    var letterspanfull = document.createElement("span")
    letterspanfull.className = randomLetter(hiddenClass)

    if (!Math.round(Math.random(letters[letter]))) {
    var letterspanfake = document.createElement("span")
    letterspanfake.innerText = randomLetter(hiddenClass) + "\u00AD"
    letterspanfake.className = hiddenClass
    letterspanfull.appendChild(letterspanfake)
    }

    var letterspan = document.createElement("span")
    letterspan.innerHTML = letters[letter] + "\u00AD"
    if (!Math.round(Math.random())) {
    if (!Math.round(Math.random())) {
    letterspan.innerHTML = letters[letter] + "\u00AD" + "&#8205;"
    } else {
    letterspan.innerHTML = "&#8205;" + letters[letter] + "\u00AD"
    }
    }
    letterspan.className = randomLetter(hiddenClass)
    letterspanfull.appendChild(letterspan)

    wordspan.appendChild(letterspanfull)
    }
    
    result.appendChild(document.createTextNode(" "))
    result.appendChild(wordspan)
    }

    var hiddenClassElem = document.createElement("style")
    hiddenClassElem.innerHTML = `.${hiddenClass} {position: absolute;z-index: -10;opacity: 0;}`

    result = result.innerHTML + hiddenClassElem.outerHTML
    return (
    <span className={randomLetter(hiddenClass)} style={{pointerEvents: "none"}} dangerouslySetInnerHTML={{__html: result}} />
    )
}

export { Obfuscate as default }