import React from "react";
import Obfuscate from "../../components/obfuscate.js";

function Search() {
  const [currentProxy, setCurrentProxy] = React.useState(localStorage.getItem("type") || "Ultraviolet")
  function typeClass(type) {
    return currentProxy == type ? "optionchoose chooseactive" : "optionchoose"
  }
  function setType(event) {
    localStorage.setItem("type", event.target.dataset.tab)
    setCurrentProxy(event.target.dataset.tab)
  }
  const [currentEngine, setCurrentEngine] = React.useState(localStorage.getItem("engine") || "Google")
  function engineClass(type) {
    return currentEngine == type ? "optionchoose chooseactive" : "optionchoose"
  }
  function setEngine(event) {
    localStorage.setItem("engine", event.target.dataset.tab)
    setCurrentEngine(event.target.dataset.tab)
  }
  return (
      <React.Fragment>
      <div className="optiontitle"><Obfuscate>Proxy</Obfuscate></div>
      <div className="chooseoption">
      <div data-tab="Ultraviolet" onClick={setType} className={typeClass("Ultraviolet")}><Obfuscate>Ultraviolet</Obfuscate></div>
      <div data-tab="Stomp" onClick={setType} className={typeClass("Stomp")}><Obfuscate>Stomp</Obfuscate></div>
      <div data-tab="DIP" onClick={setType} className={typeClass("DIP")}><Obfuscate>DIP</Obfuscate></div>
      <div data-tab="Osana" onClick={setType} className={typeClass("Osana")}><Obfuscate>Osana</Obfuscate></div>
      <div data-tab="Aero" onClick={setType} className={typeClass("Aero")}><Obfuscate>Aero</Obfuscate></div>
      </div>
      <div className="optiontitle"><Obfuscate>Search Engine</Obfuscate></div>
      <div className="chooseoption">
      <div data-tab="Google" onClick={setEngine} className={engineClass("Google")}><Obfuscate>Google</Obfuscate></div>
      <div data-tab="DuckDuckGo" onClick={setEngine} className={engineClass("DuckDuckGo")}><Obfuscate>DuckDuckGo</Obfuscate></div>
      <div data-tab="Bing" onClick={setEngine} className={engineClass("Bing")}><Obfuscate>Bing</Obfuscate></div>
      <div data-tab="Brave" onClick={setEngine} className={engineClass("Brave")}><Obfuscate>Brave</Obfuscate></div>
      </div>
      </React.Fragment>
  );
}

export default Search;