import React from "react";
import { useNavigate, Link } from "react-router-dom";
import Nav from "../components/nav.js";
import Head from "../components/head.js";
import Obfuscate from "../components/obfuscate.js";

import { Appearance, Search, Tab, UI } from "./settings/index.js"

function Settings() {
  const navigate = useNavigate();

  const [activeTab, setActiveTab] = React.useState(["Search", "Tab", "Appearance", "UI"].includes(window.location.pathname.split("/settings/")[1] || "Search") ? window.location.pathname.split("/settings/")[1] || "Search" : "Search");
  function changeTab(event) {
    setActiveTab(event.target.dataset.tab);
    navigate("/settings/" + event.target.dataset.tab, { from: window.location.pathname })
  }

  function tabClass(tab) {
    return activeTab == tab ? "settingssidecard settingssidecardsel" : "settingssidecard"
  }

  var Activeapp;
  switch (activeTab) {
  case "Search":
    Activeapp = Search
    break;
  case "Tab":
    Activeapp = Tab
    break;
  case "Appearance":
    Activeapp = Appearance
    break;
  case "UI":
    Activeapp = UI
    break;
  default:
    Activeapp = Search
    break;
}

  return (
      <React.Fragment>
      <Head defaultTitle="Settings | Metallic"></Head>
      <Nav settings />
      <div className="settingssidebar">
      <div className="settingssidetitle"><Obfuscate>Settings</Obfuscate></div>
      <div data-tab="Search" onClick={changeTab} className={tabClass("Search")}><Obfuscate>Search</Obfuscate></div>
      <div data-tab="Tab" onClick={changeTab} className={tabClass("Tab")}><Obfuscate>Tab</Obfuscate></div>
      <div data-tab="Appearance" onClick={changeTab} className={tabClass("Appearance")}><Obfuscate>Appearance</Obfuscate></div>
      <div data-tab="UI" onClick={changeTab} className={tabClass("UI")}><Obfuscate>UI</Obfuscate></div>
      </div>
      <div className="settingsapp">
      <Activeapp />
      </div>
      
      </React.Fragment>
  );
}

export default Settings;