import React from "react";
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";

import particles from "./backgrounds/particles.json"
import stars from "./backgrounds/stars.json"
import blocks from "./backgrounds/blocks.json"
import triangles from "./backgrounds/triangles.json"
import balls from "./backgrounds/balls.json"

window.particles = particles
window.stars = stars
window.blocks = blocks
window.triangles = triangles
window.balls = balls

function Background() {
  var [ background, setBackground] = React.useState(window[localStorage.getItem("background")] || "none")
  var [ pcolor, setPcolor] = React.useState(getComputedStyle(document.body).getPropertyValue('--text').replaceAll(" ", ""))

  window.addEventListener("updateBackground", function(e) {
    setBackground(window[localStorage.getItem("background")] || "none")
  });

  const particlesInit = async (main) => {
    await loadFull(main);
  };
  return (
      <React.Fragment>
      <Particles
      id="tsparticles"
      init={particlesInit}
      style={{display: background == "none" ? "none" : "initial"}}
      options={background} />
      </React.Fragment>
  );
}

export default Background;