import React from "react";
import { Link } from "react-router-dom";
import Nav from "../components/nav.js";
import Obfuscate from "../components/obfuscate.js";
import Head from "../components/head.js";
import Proxy from "../components/proxy.js";
import BareClient from '@tomphttp/bare-client';

function Home() {
  var [proxyConfig, setProxyConfig] = React.useState({
    open: false,
    url: null,
    title: null,
    icon: null
  })

  var [ suggestions, setSuggestions ] = React.useState([])

  const bare = new BareClient(window.location.origin + "/bare/");

  var omniboxcontainer = React.useRef()

  var suggestionsChildren = React.useRef()

  var omnibox = React.useRef()

  function showOmnibox() {
    if (!omniboxcontainer.current.hasAttribute("open")) {
    omniboxcontainer.current.setAttribute("open", "")
    }
  }

  function hideOmnibox() {
    if (omniboxcontainer.current.hasAttribute("open")) {
    omniboxcontainer.current.removeAttribute("open")
    }
  }

  async function updateOmnibox(query) {
      try {
      var site = await bare.fetch("https://www.google.com/complete/search?client=gws-wiz&q=" + query)
      var results = await site.text()
      results = JSON.parse(results.replaceAll("window.google.ac.h(", "").slice(0, -1))[0]
      results.forEach((item, number) => results[number] = item[0])
      results = results.slice(0, 6)
      } catch(err) {
      console.error(err)
      var results = []
      }
      setSuggestions(results)
      if (suggestionsChildren.current.children.length == 0) {
        hideOmnibox()
      } else {
        showOmnibox()
      }
  }

  async function searchType(e) {
    var getLink = window['getLink']
    if (e.keyCode === 13) {
      var windowType = localStorage.getItem("window") || "default"
      if (windowType == "default") {
      setProxyConfig({
        open: true,
        url: getLink(e.target.value),
        title: null,
        icon: null
      })
      } else if (windowType == "simple") {
      window.location.href = getLink(e.target.value)
      } else if (windowType == "ab") {
        var page = window.open()
        page.document.body.innerHTML = `<iframe style="height:100%; width: 100%; border: none; position: fixed; top: 0; right: 0; left: 0; bottom: 0; border: none" sandbox="allow-forms allow-modals allow-orientation-lock allow-pointer-lock allow-popups allow-popups-to-escape-sandbox allow-presentation allow-same-origin allow-scripts allow-top-navigation allow-top-navigation-by-user-activation" src="` + new URL(getLink(e.target.value), window.location) + `"></iframe>`
      }
      return;
    }

    if (e.target.value && e.target.value !== "") {
      updateOmnibox(e.target.value)
      showOmnibox()
    } else {
      hideOmnibox()
      setSuggestions([])
    }
  }

  window.addEventListener("click", function(e) {
    if (!document.querySelector(".omniboxcontainer")) {
      return;
    }
  if (e.target.className !== "omnibox" && e.target.className !== "search" && e.target.className !== "searchicon" && e.target.className !== "suggestions" && e.target.className !== "sugg") {
    if (omniboxcontainer.current.hasAttribute("open")) {
    hideOmnibox()
    setSuggestions([])
  }
  } else {
    if (e.target.value && e.target.value !== "") {
    updateOmnibox(e.target.value)
    showOmnibox()
    } else {
      hideOmnibox()
      setSuggestions([])
    }
  }
  })

function suggestionGo(e) {
  var getLink = window['getLink']
    var windowType = localStorage.getItem("window") || "default"
    if (windowType == "default") {
    setProxyConfig({
      open: true,
      url: getLink(e.target.innerText),
      title: null,
      icon: null
    })
    } else if (windowType == "simple") {
    window.location.href = getLink(e.target.innerText)
    } else if (windowType == "ab") {
      var page = window.open()
      page.document.body.innerHTML = `<iframe style="height:100%; width: 100%; border: none; position: fixed; top: 0; right: 0; left: 0; bottom: 0; border: none" sandbox="allow-forms allow-modals allow-orientation-lock allow-pointer-lock allow-popups allow-popups-to-escape-sandbox allow-presentation allow-same-origin allow-scripts allow-top-navigation allow-top-navigation-by-user-activation" src="` + new URL(getLink(e.target.value), window.location) + `"></iframe>`
    }
}

  return (
      <React.Fragment>
      <Head defaultTitle="Metallic"></Head>
      <Proxy config={proxyConfig} configChange={(e) => setProxyConfig(e)} />
      <Nav />
      <div className="hometitle"><Obfuscate>Metallic</Obfuscate></div>
      <div ref={omniboxcontainer} className="omniboxcontainer">
      <div ref={omnibox} className="omnibox">
      <div className="searchicon"><svg focusable="false" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M15.5 14h-.79l-.28-.27A6.471 6.471 0 0 0 16 9.5 6.5 6.5 0 1 0 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"></path></svg></div>
      <input onKeyUp={searchType} autoComplete="off" autoFocus id="search" className="search" />
      </div>
      <div ref={suggestionsChildren} className="suggestions">
      {suggestions.map((item) => (
      <div key={item} className="sugg" onClick={suggestionGo} dangerouslySetInnerHTML={{ __html: item }}></div>
      ))}
      </div>
      </div>
      <div className="footer">
      <div>
      <Link className="footersides link" to="/privacy">Privacy</Link>
      <Link className="footersides link" to="/credits">Credits</Link>
      </div>
      <div className="footermiddle"><Obfuscate>© Metallic {new Date().getFullYear()}</Obfuscate></div>
      <div>
      <a className="footersides link" href="https://discord.gg/yk33HZSZkU">Discord</a>
      <a className="footersides link" href="https://github.com/FogNetwork/Metallic">Github</a>
      </div>
      </div>
      </React.Fragment>
  );
}

export default Home;